import React, { useEffect, useRef, useState } from "react";
import styles from "./Map.module.css";
import * as d3 from "d3";
import bus from "../../bus.png";
import dialog from "../../dialog.png";

const BUS_IMAGE_SIZE = 40;
const DIALOG_IMAGE_WIDTH = 120;

const Map = (props) => {
  const mapcontainer = useRef(null);
  const { data } = props;

  const [map, setMap] = useState(null);
  const [overlay, setOverlay] = useState(null);
  useEffect(() => {
    setMap(
      new window.google.maps.Map(mapcontainer.current, {
        center: { lat: 13.741307, lng: 100.533518 },
        zoom: 16,
      })
    );
    setOverlay(new window.google.maps.OverlayView());
  }, []);
  useEffect(() => {
    if (!mapcontainer) {
      return;
    }

    if (!map) {
      return;
    }
    if (!overlay) {
      return;
    }

    if (data.length == 0) {
      return;
    }

    const route = [
      [
        [13.735406, 100.531831],
        [13.734605, 100.531697],
        [13.734195, 100.534123],
        [13.745312, 100.535775],
        [13.746148, 100.530848],
        [13.739681, 100.529821],
        [13.739176, 100.532807],
        [13.73903, 100.533089],
        [13.73891, 100.533204],
        [13.737891, 100.533061],
        [13.737417, 100.532536],
        [13.737479, 100.532073],
        [13.735406, 100.531831],
      ],
    ];
    if (d3.select("text.textvelo").select("tspan.velo").empty()) {
      var text = d3.select("text.textvelo");

      text
        .append("tspan")
        .attr("class", "velo")
        .text(data[0].v)
        .style("font-size", 32)
        .style("font-weight", 600)
        .attr("fill", "white");
      text
        .append("tspan")
        .style("font-size", 18)
        .style("font-weight", 400)
        .attr("fill", "white")
        .text(" km/hr");
    } else {
      d3.select("text.textvelo").select("tspan.velo").text(data[0].v);
    }

    overlay.setMap(null);
    overlay.onAdd = function () {
      //   var layer = d3
      //     .select(this.getPanes().overlayLayer)
      //     .append("div")
      //     .attr("class", "stations");

      let firsttime = true;
      let firsttime_velo = true;
      let firsttime_text = true;
      if (d3.select(mapcontainer.current).select("svg").empty()) {
        var layer = d3
          .select(mapcontainer.current)
          .append("svg")
          .attr("width", mapcontainer.current.offsetWidth)
          .attr("height", mapcontainer.current.offsetHeight)
          .style("top", 0)
          .style("left", 0)
          .style("position", "absolute")
          .style("pointer-events", "none");
      } else {
        layer = d3.select(mapcontainer.current).select("svg");
      }

      // Draw each marker as a separate SVG element.
      // We could use a single SVG, but what size would it have?
      overlay.draw = function () {
        var projection = this.getProjection();

        const busroute = layer
          .selectAll("path.route")
          .data(route)
          .each(routetransform)
          .enter()
          .append("path")
          .each(routetransform)
          .attr("stroke", "#f06eaa")
          .attr("stroke-width", "4")
          .attr("fill", "none")
          .attr("class", "route");

        var marker = layer
          .selectAll("image.marker")
          .data(data)
          .each(transform) // update existing markers

          .enter()
          .append("svg:image")
          .each(transform)
          .attr("xlink:href", bus)
          .attr("width", BUS_IMAGE_SIZE)
          .attr("height", BUS_IMAGE_SIZE)

          .attr("class", "marker");
        var velocity = layer
          .selectAll("image.velocitywrapper")
          .data(data)
          .each(vtransform) // update existing markers

          .enter()
          .append("svg:image")
          .each(vtransform)
          .attr("xlink:href", dialog)
          .attr("width", DIALOG_IMAGE_WIDTH)
          .attr("class", "velocitywrapper");
        var velocitytext = layer
          .selectAll("text")
          .data(data)
          .each(texttransform) // update existing markers
          .enter()
          .append("text")
          .attr("class", "textvelo")
          .each(texttransform);

        function routetransform(d) {
          let path = "";
          for (let i in d) {
            let latlong = new window.google.maps.LatLng(d[i][0], d[i][1]);
            const { x, y } = projection.fromLatLngToContainerPixel(latlong);
            if (i == 0) {
              path += "M" + x + "," + y + " ";
              continue;
            }
            path += "L" + x + "," + y + " ";
          }

          return d3.select(this).attr("d", path);
        }

        function transform(d) {
          d = new window.google.maps.LatLng(d.lat, d.lng);
          d = projection.fromLatLngToContainerPixel(d);

          if (firsttime) {
            firsttime = false;
            return d3
              .select(this)
              .transition()
              .duration(1000)
              .attr("x", d.x - BUS_IMAGE_SIZE / 2 + "px")
              .attr("y", d.y - BUS_IMAGE_SIZE / 2 + "px");
          } else {
            return d3
              .select(this)
              .attr("x", d.x - BUS_IMAGE_SIZE / 2 + "px")
              .attr("y", d.y - BUS_IMAGE_SIZE / 2 + "px");
          }
        }

        function vtransform(d) {
          d = new window.google.maps.LatLng(d.lat, d.lng);
          d = projection.fromLatLngToContainerPixel(d);

          if (firsttime_velo) {
            firsttime_velo = false;
            return d3
              .select(this)
              .transition()
              .duration(1000)
              .attr("x", d.x + "px")
              .attr("y", d.y - DIALOG_IMAGE_WIDTH + 5 + "px");
          } else {
            return d3
              .select(this)
              .attr("x", d.x + "px")
              .attr("y", d.y - DIALOG_IMAGE_WIDTH + 5 + "px");
          }
        }
        function texttransform(d) {
          let data = new window.google.maps.LatLng(d.lat, d.lng);

          data = projection.fromLatLngToContainerPixel(data);

          if (firsttime_text) {
            firsttime_text = false;
            return d3
              .select(this)
              .transition()
              .duration(1000)

              .attr("x", data.x + 5 + "px")
              .attr("y", data.y - DIALOG_IMAGE_WIDTH + 30 + 18 + "px");
          } else {
            return d3
              .select(this)

              .attr("x", data.x + 5 + "px")
              .attr("y", data.y - DIALOG_IMAGE_WIDTH + 30 + 18 + "px");
          }
        }
      };
    };
    overlay.onRemove = function () {
      console.log("removing");
    };

    // Bind our overlay to the map…
    overlay.setMap(map);
  }, [data, map, overlay]);
  return (
    <div ref={mapcontainer} style={{ width: "100%", height: "100%" }}></div>
  );
};

export default Map;
