import React, { useEffect, useState } from "react";
import logo from "./logo.svg";
import "./App.css";
import LineChart from "./Components/LineChart/LineChart";
import Card from "./Components/Card/Card";
import Gauge from "./Components/Gauge/Gauge";
import openSocket from "socket.io-client";
import Map from "./Components/Map/Map";
import * as d3 from "d3";

import { getDistance } from "geolib";

var socket;

const time_parse = d3.timeParse("%m/%d/%Y %H:%M:%S");
const time_format = d3.timeFormat("%Y");

function App() {
  const [data, setData] = useState([]);
  const [location, setLocation] = useState([]);
  const [gaugeData, setGaugeData] = useState([]);
  useEffect(() => {
    socket = openSocket("http://178.128.16.9/");
    var socketio = socket.connect();
    socket.on("connect", () => {
      console.log("check", socketio.connected);
    });
    socket.on("disconnect", () => {
      console.log("check", socketio.connected);
    });
    socket.on("numclient", (data) => {
      console.log(data);
    });
    const velocity = [];
    let lastLocation = 0;
    let lastTime = 0;
    socket.on("message", (data) => {
      try {
        const sensor_data = JSON.parse(data.msg);
        const { Lat, Lng, dev_id } = sensor_data;

        console.log(dev_id);

        if (dev_id != "0001") {
          return;
        }
        console.log(Lat);
        console.log(Lng);
        console.log(Date.now());
        if (lastLocation != 0) {
          if (velocity.length == 3) {
            velocity.shift();
          }
          velocity.push(
            (((getDistance(
              lastLocation,
              { latitude: Lat, longitude: Lng },
              0.01
            ) /
              (Date.now() - lastTime)) *
              5) /
              18) *
              1000
          );
          console.log(velocity);
        }
        var average = (arr) => arr.reduce((p, c) => p + c, 0) / arr.length;
        lastLocation = { latitude: Lat, longitude: Lng };
        lastTime = Date.now();
        setLocation([{ lat: Lat, lng: Lng, v: Math.round(average(velocity)) }]);
        setGaugeData([
          sensor_data.PM1_0,
          sensor_data.PM2_5,
          sensor_data.PM10_0,
        ]);
        setData(changeData.bind(this, sensor_data));
      } catch (e) {
        console.log(e);

        return;
      }
    });

    setLocation([{ lat: 13.740766, lng: 100.535039, v: 100 }]);

    const changeData = (data, curData) => {
      const newData = [...curData];

      if (curData.length < 10) {
        newData.push({
          val1: data.PM1_0,
          val2: data.PM2_5,
          val3: data.PM10_0,
          date: Date.now(),
        });
      } else {
        newData.push({
          val1: data.PM1_0,
          val2: data.PM2_5,
          val3: data.PM10_0,
          date: Date.now(),
        });
        newData.shift();
      }

      return newData;
    };
  }, []);

  return (
    <div className="App">
      <div>
        <div>
          <h1
            style={{
              fontSize: 80,
              marginTop: 5,
              marginBottom: 5,
              marginLeft: 20,
              color: "white",
            }}
          >
            NB-<span style={{ fontWeight: 400 }}>IoT</span>
          </h1>
        </div>
        <Card width={900}>
          <h2>PM chart</h2>
          <LineChart
            data={data}
            width={800}
            height={400}
            padding={50}
          ></LineChart>
          <div className="legends-container">
            <div className="legend-container">
              <div
                style={{
                  borderTop: "solid 3px red",
                  width: 25,
                  marginRight: 5,
                }}
              ></div>
              <p style={{ margin: 0 }}>PM 1</p>
            </div>
            <div className="legend-container">
              <div
                style={{
                  borderTop: "solid 3px blue",
                  width: 25,
                  marginRight: 5,
                }}
              ></div>
              <p style={{ margin: 0 }}>PM 2.5</p>
            </div>
            <div className="legend-container">
              <div
                style={{
                  borderTop: "solid 3px green",
                  width: 25,
                  marginRight: 5,
                }}
              ></div>
              <p style={{ margin: 0 }}>PM 10</p>
            </div>
          </div>
        </Card>
        <div style={{ display: "flex" }}>
          <Card width={287}>
            <h2>PM 1</h2>
            <Gauge gauge_id="pm1" data={gaugeData[0]}></Gauge>
            <h1
              style={{
                margin: 0,
                top: -30,
                position: "relative",
                fontSize: 50,
              }}
            >
              {gaugeData[0]}
              <span style={{ fontSize: 30 }}>
                {" "}
                &micro;g/m<sup>3</sup>
              </span>
            </h1>
          </Card>
          <Card width={287}>
            <h2>PM 2.5</h2>
            <Gauge gauge_id="pm25" data={gaugeData[1]}></Gauge>
            <h1
              style={{
                margin: 0,
                top: -30,
                position: "relative",
                fontSize: 50,
              }}
            >
              {gaugeData[1]}
              <span style={{ fontSize: 30 }}>
                {" "}
                &micro;g/m<sup>3</sup>
              </span>
            </h1>
          </Card>
          <Card width={287}>
            <h2>PM 10</h2>
            <Gauge gauge_id="pm10" data={gaugeData[2]}></Gauge>
            <h1
              style={{
                margin: 0,
                top: -30,
                position: "relative",
                fontSize: 50,
              }}
            >
              {gaugeData[2]}
              <span style={{ fontSize: 30 }}>
                {" "}
                &micro;g/m<sup>3</sup>
              </span>
            </h1>
          </Card>
        </div>
      </div>

      <Card width={900}>
        <Map data={location}></Map>
      </Card>
    </div>
  );
}

const generateData = (n) => {
  let results = [];

  for (let i = 0; i <= n; i++) {
    results.push({
      date: time_parse(2020 - n + i),
      val: Math.random() * 1000,
    });
  }
  return results;
};

const createChart = () => {
  const data = generateData(10);
  data.forEach(function (e, i) {
    data[i].date = time_parse(e.date);
  });
  let i = 1;
  setInterval(() => {
    data.push({ date: time_parse(2020 + i), val: Math.random() * 1000 });

    data.shift();
  }, 2000);
};

export default App;
