import React, { useEffect, useState } from "react";
import styles from "./Gauge.module.css";
import * as d3 from "d3";
const Gauge = (props) => {
  const { data } = props;
  const [gaugecom, setGaugeCom] = useState(null);
  var gauge = function (container, configuration) {
    var that = {};
    var config = {
      size: 710,
      clipWidth: 200,
      clipHeight: 110,
      ringInset: 20,
      ringWidth: 20,

      pointerWidth: 10,
      pointerTailLength: 5,
      pointerHeadLengthPercent: 0.9,

      minValue: 0,
      maxValue: 40,

      minAngle: -90,
      maxAngle: 90,

      transitionMs: 750,

      majorTicks: 5,
      labelFormat: d3.format("d"),
      labelInset: 10,

      arcColorFn: d3
        .scaleLinear()
        .domain([0, 0.5, 1])
        .range(["#1a9850", "#fee08b", "#d73027"])
        .interpolate(d3.interpolateHsl),
    };
    var range = undefined;
    var r = undefined;
    var pointerHeadLength = undefined;
    var value = 0;

    var svg = undefined;
    var arc = undefined;
    var scale = undefined;
    var ticks = undefined;
    var tickData = undefined;
    var pointer = undefined;

    var donut = d3.pie();

    function deg2rad(deg) {
      return (deg * Math.PI) / 180;
    }

    function newAngle(d) {
      var ratio = scale(d);
      var newAngle = config.minAngle + ratio * range;
      return newAngle;
    }

    var markers = [0, 10, 20, 30, 40, 50, 60, 70, 80];

    function configure(configuration) {
      var prop = undefined;
      for (prop in configuration) {
        config[prop] = configuration[prop];
      }

      range = config.maxAngle - config.minAngle;
      r = config.size / 2;
      pointerHeadLength = Math.round(r * config.pointerHeadLengthPercent);

      // a linear scale that maps domain values to a percent from 0..1
      const linearScale = d3.scaleLinear().range([0, 1]).domain([0, 10]);

      scale = function (val) {
        var idx = markers.length;
        for (var i = markers.length - 1; i >= 0; i--) {
          idx = i;
          if (val >= markers[i]) {
            break;
          }
        }

        var minVal = markers[idx];
        var maxVal = markers[markers.length - 1];

        if (val < markers[markers.length - 1]) {
          maxVal = markers[idx + 1];
        }
        return d3
          .scaleLinear()
          .range([
            idx * (1.0 / config.majorTicks),
            (idx + 1) * (1.0 / config.majorTicks),
          ])
          .domain([minVal, maxVal])(val);
      };

      ticks = linearScale.ticks(config.majorTicks);
      tickData = d3.range(config.majorTicks).map(function () {
        return 1 / config.majorTicks;
      });

      arc = d3
        .arc()
        .innerRadius(r - config.ringWidth - config.ringInset)
        .outerRadius(r - config.ringInset)
        .startAngle(function (d, i) {
          var ratio = d * i;
          return deg2rad(config.minAngle + ratio * range);
        })
        .endAngle(function (d, i) {
          var ratio = d * (i + 1);
          return deg2rad(config.minAngle + ratio * range);
        });
    }
    that.configure = configure;

    function centerTranslation() {
      return "translate(" + r + "," + r + ")";
    }

    function isRendered() {
      return svg !== undefined;
    }
    that.isRendered = isRendered;

    function render(newValue) {
      svg = d3
        .select(container)
        .append("svg:svg")
        .attr("class", "gauge")
        .attr("width", config.clipWidth)
        .attr("height", config.clipHeight);

      var centerTx = centerTranslation();

      var arcs = svg
        .append("g")
        .attr("class", "arc")
        .attr("transform", centerTx);

      arcs
        .selectAll("path")
        .data(tickData)
        .enter()
        .append("path")
        .attr("fill", function (d, i) {
          return config.arcColorFn(d * i);
        })
        .attr("d", arc);

      var lg = svg
        .append("g")
        .attr("class", "label")
        .attr("transform", centerTx);
      lg.selectAll("text")
        //.data(ticks)
        .data(markers)
        .enter()
        .append("text")
        .attr("transform", function (d, i) {
          // var ratio = scale(d);
          var ratio = i / config.majorTicks;
          var newAngle = config.minAngle + ratio * range;
          return (
            "rotate(" +
            newAngle +
            ") translate(0," +
            (config.labelInset - r) +
            ")"
          );
        })
        .text(config.labelFormat);

      var lineData = [
        [config.pointerWidth / 2, 0],
        [0, -pointerHeadLength],
        [-(config.pointerWidth / 2), 0],
        [0, config.pointerTailLength],
        [config.pointerWidth / 2, 0],
      ];
      var pointerLine = d3.line().curve(d3.curveLinear);
      var pg = svg
        .append("g")
        .data([lineData])
        .attr("class", "pointer")
        .attr("transform", centerTx);

      pointer = pg
        .append("path")
        .attr("d", pointerLine /*function(d) { return pointerLine(d) +'Z';}*/)
        .attr("transform", "rotate(" + config.minAngle + ")");

      update(newValue === undefined ? 0 : newValue);
    }
    that.render = render;
    function update(newValue, newConfiguration) {
      if (newConfiguration !== undefined) {
        configure(newConfiguration);
      }
      var ratio = scale(newValue);
      var newAngle = config.minAngle + ratio * range;
      pointer
        .transition()
        .duration(config.transitionMs)
        .ease(d3.easeElastic)
        .attr("transform", "rotate(" + newAngle + ")");
    }
    that.update = update;

    configure(configuration);

    return that;
  };

  useEffect(() => {
    if (!gaugecom) {
      setGaugeCom(
        gauge("#" + props.gauge_id, {
          size: 250,
          clipWidth: 250,
          clipHeight: 180,
          ringWidth: 50,
          maxValue: 60,
          transitionMs: 2000,
          majorTicks: 8,
        })
      );
    }
  }, []);
  useEffect(() => {
    if (gaugecom) {
      console.log(gaugecom.isRendered());
      if (!gaugecom.isRendered()) {
        gaugecom.render();
        gaugecom.update(data);
      }
      console.log(data);
      gaugecom.update(data);
    }
  }, [gaugecom, data]);

  return <div id={props.gauge_id}></div>;
};

export default Gauge;
