import React, { useEffect, useState } from "react";
import styles from "./LineChart.module.css";
import * as d3 from "d3";

const time_format = d3.timeFormat("%H:%M:%S");

const LineChart = props => {
  const [chartCreated, setChartCreated] = useState(false);
  const [firstTimeAnimation, setFirstTimeAnimation] = useState(true);
  const [lastLineLength, setLastLineLength] = useState(0);
  const { data } = props;

  useEffect(() => {
    if (!chartCreated) {
      createChart(data, props.width, props.height, props.padding);
      setChartCreated(true);
    } else {
      if (data.length != 0) {
        updateChart("line1", data, props.width, props.height, props.padding);
      }
    }
  }, [data]);
  const createChart = (data, CHART_WIDTH, CHART_HEIGHT, PADDING) => {
    const svg = d3.select("svg");
    const x_scale = d3
      .scaleTime()
      .domain([
        d3.min(data, d => {
          return d.date;
        }),
        d3.max(data, d => {
          return d.date;
        })
      ])
      .range([PADDING, CHART_WIDTH - PADDING]);

    var y_scale = d3
      .scaleLinear()
      .domain([
        0,
        d3.max(data, function(d) {
          return d.val;
        })
      ])
      .range([CHART_HEIGHT - PADDING, PADDING]);

    // Create SVG

    var x_axis = d3
      .axisBottom(x_scale)

      .tickFormat(time_format);
    var y_axis = d3.axisLeft(y_scale);
    svg
      .append("defs")
      .append("clipPath")
      .attr("id", "clip")
      .append("rect")
      .attr("transform", "translate(" + PADDING + "," + -PADDING + ")")
      .attr("height", props.height);
    svg
      .append("g")
      .attr("class", "x-axis")
      .attr("transform", "translate(0," + (CHART_HEIGHT - PADDING) + ")")
      .call(x_axis)
      .transition()
      .duration(1000);
    svg
      .append("g")
      .attr("transform", "translate(" + PADDING + ",0)")
      .attr("class", "y-axis")
      .call(y_axis)
      .transition()
      .duration(1000);
    createLine("line1", svg, data, x_scale, y_scale, "red");
    createLine("line2", svg, data, x_scale, y_scale, "blue");
    createLine("line3", svg, data, x_scale, y_scale, "green");
  };
  const createLine = (id, svg, data, x_scale, y_scale, color) => {
    // Create Line
    var line = d3
      .line()
      .x(function(d) {
        return x_scale(d.date);
      })
      .y(function(d) {
        return y_scale(d.val);
      });

    const path = svg
      .append("g")
      .attr("clip-path", "url(#clip)")

      .append("path")
      .attr("class", id)
      .datum(data)
      .attr("fill", "none")
      .attr("stroke", color)
      .attr("stroke-width", 2)
      .attr("d", line);
  };

  const updateChart = (id, data, CHART_WIDTH, CHART_HEIGHT, PADDING) => {
    if (data.length < 3) {
      return;
    }
    const x_axis_data = [...data];
    x_axis_data.shift();

    const x_scale = d3
      .scaleTime()
      .domain([
        d3.min(x_axis_data, d => {
          return d.date;
        }),
        d3.max(x_axis_data, d => {
          return d.date;
        })
      ])
      .range([PADDING, CHART_WIDTH - PADDING]);

    const tickSize = x_scale(data[2].date) - x_scale(data[1].date);

    const clipath = d3
      .select("#clip rect")
      .attr("width", props.width - PADDING - PADDING + 1);
    const chart_scale = d3
      .scaleTime()
      .domain([
        d3.min(data, d => {
          return d.date;
        }),
        d3.max(data, d => {
          return d.date;
        })
      ])
      .range([PADDING, CHART_WIDTH - PADDING + tickSize]);

    var y_scale = d3
      .scaleLinear()
      .domain([
        0,
        d3.max(data, function(d) {
          const newData = { ...d };
          newData.date = 0;
          let arr = Object.values(newData);
          return Math.max(...arr);
        })
      ])
      .range([CHART_HEIGHT - PADDING, PADDING]);

    var x_axis = d3
      .axisBottom(x_scale)
      .ticks(5)
      .tickFormat(time_format);
    var y_axis = d3.axisLeft(y_scale).ticks(12);

    //line 1
    var line = d3
      .line()
      .x(function(d) {
        return chart_scale(d.date);
      })
      .y(function(d) {
        return y_scale(d.val1);
      });

    d3.select(".x-axis")

      .transition()
      .duration(1000)
      .call(x_axis);

    d3.select(".y-axis")
      .transition()
      .duration(1000)
      .call(y_axis);

    const path = d3
      .select("." + id)
      .datum(data)
      .attr("d", line);
    const totalLength = path.node().getTotalLength();

    if (firstTimeAnimation) {
      path
        .attr("stroke-dasharray", totalLength + " " + totalLength)
        .attr("stroke-dashoffset", totalLength)
        .attr("transform", "translate(" + -tickSize + ",0)")
        .transition()
        .duration(2000)
        .attr("stroke-dashoffset", 0);
      setFirstTimeAnimation(false);
    } else {
      path
        .attr("stroke-dasharray", null)
        .attr("stroke-dashoffset", null)
        .attr("transform", "translate(" + 0 + ",0)")
        .transition()
        .duration(1000)
        .attr("transform", "translate(" + -tickSize + ",0)")
        .attr("stroke-dashoffset", 0);
    }

    // line 2
    var line2 = d3
      .line()
      .x(function(d) {
        return chart_scale(d.date);
      })
      .y(function(d) {
        return y_scale(d.val2);
      });

    const path2 = d3
      .select(".line2")
      .datum(data)
      .attr("d", line2);
    const totalLength2 = path2.node().getTotalLength();

    if (firstTimeAnimation) {
      path2
        .attr("stroke-dasharray", totalLength2 + " " + totalLength2)
        .attr("stroke-dashoffset", totalLength2)
        .attr("transform", "translate(" + -tickSize + ",0)")
        .transition()
        .duration(2000)
        .attr("stroke-dashoffset", 0);
      setFirstTimeAnimation(false);
    } else {
      path2
        .attr("stroke-dasharray", null)
        .attr("stroke-dashoffset", null)
        .attr("transform", "translate(" + 0 + ",0)")
        .transition()
        .duration(1000)
        .attr("transform", "translate(" + -tickSize + ",0)")
        .attr("stroke-dashoffset", 0);
    }

    // line 3
    var line3 = d3
      .line()
      .x(function(d) {
        return chart_scale(d.date);
      })
      .y(function(d) {
        return y_scale(d.val3);
      });

    const path3 = d3
      .select(".line3")
      .datum(data)
      .attr("d", line3);
    const totalLength3 = path3.node().getTotalLength();

    if (firstTimeAnimation) {
      path3
        .attr("stroke-dasharray", totalLength3 + " " + totalLength3)
        .attr("stroke-dashoffset", totalLength3)
        .attr("transform", "translate(" + -tickSize + ",0)")
        .transition()
        .duration(2000)
        .attr("stroke-dashoffset", 0);
      setFirstTimeAnimation(false);
    } else {
      path3
        .attr("stroke-dasharray", null)
        .attr("stroke-dashoffset", null)
        .attr("transform", "translate(" + 0 + ",0)")
        .transition()
        .duration(1000)
        .attr("transform", "translate(" + -tickSize + ",0)")
        .attr("stroke-dashoffset", 0);
    }
  };

  return (
    <div id="line_chart" className={styles.LineChart}>
      <svg
        width={props.width}
        height={props.height}
        padding={props.padding}
      ></svg>
    </div>
  );
};

export default LineChart;
